export default [{
    path: '/login',
    name: 'Login',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      notWithoutAuth: true,
      title: ''
    },
    component: () => import('@/pages/Auth/Login.vue')
  },
  {
    path: '/registration',
    name: 'Registration',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: ''
    },
    component: () => import('@/pages/Auth/Registration.vue')
  },
  {
    path: '/registration-success',
    name: 'RegisterSuccess',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: ' '
    },
    component: () => import('@/pages/Auth/RegisterSuccess.vue')
  },
  {
    path: '/forgot',
    name: 'Forgot',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: ''
    },
    component: () => import('@/pages/Auth/Forgot.vue')
  },
  {
    path: '/forgot-success',
    name: 'ForgotSuccess',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: ' '
    },
    component: () => import('@/pages/Auth/ForgotSuccess.vue')
  },
  {
    //test
    path: '/change-password/:token',
    name: 'ChangePassword',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: ' '
    },
    component: () => import('@/pages/Auth/ChangePassword.vue')
  },
  //test
  {
    path: '/forgot-expired',
    name: 'ForgotExpired',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: ' '
    },
    component: () => import('@/pages/Auth/ForgotExpired.vue')
  },
  {
    path: '/change-password-success',
    name: 'ChangePasswordSuccess',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: '  '
    },
    component: () => import('@/pages/Auth/ChangePasswordSuccess.vue')
  },
  //test
  {
    path: '/change-password-failed',
    name: 'ChangePasswordFailed',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: '   '
    },
    component: () => import('@/pages/Auth/ChangePasswordFailed.vue')
  },
  //test
  {
    path: '/forgot-failed',
    name: 'ForgotFailed',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: '  '
    },
    component: () => import('@/pages/Auth/ForgotFailed.vue')
  }
]



