import axios from 'axios'
import store from '@/store'

const NODE_ENV = process.env.NODE_ENV ? process.env.NODE_ENV : 'development';

axios.defaults.headers['content-type'] = 'application/json';
axios.defaults.withCredentials = true;
switch (NODE_ENV) {
  case 'development':
    axios.defaults.baseURL =  'https://team13.herokuapp.com/api/v1/';
    break;
  case 'production':
    axios.defaults.baseURL =  'https://team13.herokuapp.com/api/v1/';
    break;
  default:
    axios.defaults.baseURL =  'https://team13.herokuapp.com/api/v1/';
}

const token = localStorage.getItem('user-token')
if (token) axios.defaults.headers.common['Authorization'] = token

axios.interceptors.response.use(null, error => {
  //          
  // store.dispatch('auth/api/logout')

  console.log(error.response)

  store.dispatch('global/alert/setAlert', {
    status: 'error',
    text: error.response.data.error_description || error.response.data.error || error.response.statusText
  })
  return Promise.reject(error)
});



